body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  @include reg;
  color: $black;
  min-width: 320px;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm; } }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit;
    font-size: inherit; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.animated-img {
  &[data-loaded="true"] {
    animation: fadeIn 1s; } }

.overflow-hidden {
  overflow: hidden; }

.out {
  &.has-fixed-header {
    padding-top: $header-height;
    @include sm {
      padding-top: $header-height-sm; } } }

.container {
  display: block;
  max-width: 1440px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  // +xlmin
 }  //   max-width: 1440px

.logo {
  display: block;
  color: $white;
  font-size: 0;
  .icon {
    @include css-lock(21, 33, 320, 575, font-size); } }

.scroll-to {
  width: 90px;
  height: 90px;
  background-color: $yellow;
  position: relative;
  color: $white;
  @include flex(center, center);
  flex: 0 0 90px;
  @include sm {
    width: 70px;
    height: 70px;
    flex: 0 0 70px; }
  .icon {
    font-size: $fz-arrow;
    @include tr(transform .5s);
    @include sm {
      font-size: $fz-arrow-sm; } }
  &--down {
    margin-top: 20px;
    .icon {
      transform: translate(0, -100%) rotate(90deg); }
    @include sm {
      margin-top: 15px; }
    @include hover {
      .icon {
        transform: translate(0, 0) rotate(90deg); } } }
  &--up {
    align-items: flex-end;
    .icon {
      transform: rotate(-90deg); }
    @include hover {
      .icon {
        transform: translate(0, -50%) rotate(-90deg); } } } }

.word {
  // +black
  @include bold;
  @include css-lock(70, 236, 320, 1499, font-size);
  line-height: .9;
  letter-spacing: -.05em;
  text-transform: uppercase;
  // -webkit-text-fill-color: transparent
  // -webkit-background-clip: text
  // +image('black-pattern.png')
  // background-repeat: repeat
  // background-size: auto
  // color: $gray-lighten
  position: absolute;
  z-index: 0;
  opacity: 0;
  &__inner {
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    @include image('black-pattern.png');
    background-repeat: repeat;
    background-size: auto;
    color: $gray-lighten; }

  &--sm {
    @include css-lock(70, 170, 320, 1499, font-size); } }

.title-block {
  $this: &;
  position: relative;
  @include css-lock(30, 100, 320, 1499, padding-top);
  @include css-lock(30, 100, 320, 1499, padding-bottom);
  overflow: hidden;

  &__title {
    position: relative;
    z-index: 1;
    padding-left: percentage(1/12);
    opacity: 0;
    @include xs {
      padding-left: 0; } }
  &__word {
    z-index: 1;
    right: 0;
    bottom: 0; }
  &__square-wrap {
    @extend .container;
    z-index: 0;
    pointer-events: none;
    width: 100%;
    height: 100%;
    top: 0;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    overflow: hidden;
    @include before {
      bottom: 0;
      top: 0;
      left: 0;
      position: absolute;
      z-index: 0;
      background-color: $yellow;
      width: 100%;
      transform: translate(-100%, 0); }
    @include xs {
      @include before {
        width: percentage(4/12); } } }

  &--btn {
    background-color: $gray-lighten;
    overflow: visible;
    #{$this}__word {
      transform: translate(0, 50%);
      @include xs {
        display: none; } }
    #{$this}__btn {
      @extend .container;
      width: 100%;
      position: absolute;
      z-index: 1;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 50%);
      opacity: 0; }
    #{$this}__title {
      padding-left: 0; } } }


.link-arrow {
  @include bold;
  text-transform: uppercase;
  @include hover {
    .icon {
      transform: translate(15px, 0); } }
  .icon {
    font-size: 8px;
    margin-left: 15px;
    fill: $gray-light;
    @include tr(transform .3s); } }

.scrolled-text {
  font-size: 10px;
  max-height: 460px;
  overflow: hidden;
  position: relative;
  padding-right: 8px;
  .ps {
    &:hover {
      // .ps__rail-y
 }      //   opacity: 1 !important
    &__rail-y {
      opacity: 1 !important;
      background-color: transparent;
      width: 6px;
      &:hover {
        background-color: transparent; }
      @include before {
        position: absolute;
        height: 100%;
        z-index: 0;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: 2px;
        background-color: $gray-dark; } }
    &__thumb-y {
      width: 100% !important;
      left: 0;
      border-radius: 0;
      background-color: $gray-dark !important; } }
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 25px;
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } }
  h1 {
    @extend .h6; }
  h2 {
    @extend .h6;
    @extend .h6--ttn;
    @extend .h6--lg; }
  p {
    margin-bottom: 20px;
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } } }

.overlayed {
  position: relative;
  overflow: hidden;
  @include before {
    @include coverdiv;
    z-index: 2;
    background-color: $yellow;
    transform: translate(-100%, 0); } }
