.nav {
  $this: &;
  @include light;
  li {
    display: inline-block;
    position: relative;
    @include notlast {
      margin-right: 45px; }
    &.has-dropdown {
      a {
        position: relative;
        padding-right: 15px;
        @include before {
          @include icon-arrow(8, 8, 1, currentColor, -45);
          position: absolute;
          top: .2em;
          right: 0; } }
      &:hover {
        #{$this}--dropdown {
          visibility: visible;
          opacity: 1; } } } }
  a {
    @include tr(color .3s);
    text-transform: uppercase;
    display: inline-block;
    @include hover {
      color: $gray; } }

  &--dropdown {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: 1;
    top: calc(100%);
    padding-top: 25px;
    left: 0;
    color: $gray;
    white-space: nowrap;
    @include tr(opacity .3s, visibility .3s);
    li {
      display: block;
      @include notlast {
        margin-bottom: 15px; } }
    a {
      text-transform: none;
      @include before {
        display: none; }
      @include hover {
        color: $white; } } }

  &--lg {
    // color: $white
    font-size: 24px;
    text-transform: uppercase;
    @include bold;
    li {
      display: block;
      position: relative;
      padding-left: 45px;
      @include notlast {
        margin-right: 0;
        margin-bottom: 65px; }
      span {
        font-size: $fz;
        @include reg;
        position: absolute;
        left: 0;
        top: .5em;
        @include rotate(-90); } }
    a {
      position: relative;
      overflow: hidden;
      @include hover {
        color: inherit;
        @include before {
          transform: translate(0, 0); } }
      @include before {
        height: 6px;
        left: -5px;
        right: -5px;
        position: absolute;
        z-index: -1;
        background-color: $yellow;
        bottom: .3em;
        transform: translate(-100%, 0);
        @include tr(transform .3s); } } }

  &--sm {
    // color: $white
    li {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: 40px; } }
    a {
      text-transform: none;
      overflow: hidden;
      position: relative;
      @include hover {
        color: inherit;
        @include before {
            transform: translate(0, 0); } }
      @include before {
        height: 4px;
        left: -5px;
        right: -5px;
        position: absolute;
        z-index: -1;
        background-color: $yellow;
        bottom: .3em;
        transform: translate(-100%, 0);
        @include tr(transform .3s); } } } }
