.clients {
  overflow: hidden;
  position: relative;
  &__table {}
  &__word {
    top: 50%;
    transform: translate(0, -50%);
    @include md {
      top: auto;
      transform: translate(0, 0);
      bottom: 0; } } }


.clients-table {
  // +flex(flex-start, normal, row, wrap)
  width: percentage(8/12);
  position: relative;
  @include lg {
    width: percentage(10/12); }
  @include md {
    width: 100%;
    @include before {
      display: none; } }
  @include before {
    height: 1px;
    background-color: $c-border;
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translate(0, -50%);
    width: 100vw; }
  @include clr;
  &__item {
    float: left;
    width: 25%;
    @include flex(center, center);
    height: 170px;
    border: 1px solid $c-border;
    padding: 10px;
    opacity: 0;
    @include md {
      width: 33.33333%; }
    @include xs {
      width: 50%; } }
  &__item-lg {
    width: 50%;
    height: 340px;
    @include css-lock(40, 160, 320, 1499, font-size);
    @include bold;
    @include md {
      width: 100%;
      height: auto; }
    @include xs {
      width: 100%; } } }
