.section {
  position: relative;
  &--white-color {
    color: $white; }
  &--black {
    background-color: $black; } }

.s-more {
  .word {
    left: 0;
    @include css-lock(30, 125, 320, 1499, top); }
  .container {
    position: relative;
    z-index: 1; }
  &__title {
    @include css-lock(20, 175, 320, 1499, margin-bottom);
    max-width: 720px;
    .h3 {
      opacity: 0; } }
  &__block {
    width: percentage(8/12);
    margin-left: percentage(4/12);
    position: relative;
    @include md {
      width: percentage(10/12);
      margin-left: percentage(2/12); }
    @include xs {
      width: auto;
      margin-left: 0; } }
  &__block-title {
    margin-bottom: 30px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__text {
    margin-bottom: 50px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; } }
  &__title-lg {
    margin-bottom: 90px;
    @include xl {
      margin-bottom: 70px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__btn {
    text-align: right; }
  &__arrow {
    position: absolute;
    bottom: 0;
    left: 0% - percentage(360/930);
    color: $c-border;
    .icon {
      font-size: $fz-arrow;
      @include rotate(90);
      @include sm {
        font-size: $fz-arrow-sm; } }
    @include md {
      left: -16%; }
    @include xs {
      position: static;
      display: block;
      width: 40px;
      margin-top: 10px;
      margin-bottom: 10px;
      margin-left: auto;
      margin-right: auto; } } }


.s-portfolio {
  .word {
    right: -5%;
    @include sm {
      right: -10%; }
    @include xs {
      right: -20%; }
    @include xss {
      right: -30%; } }
  &__link {
    text-transform: uppercase;
    display: inline-block;
    text-align: center;
    @include bold;
    .icon {
      display: block;
      margin-left: auto;
      margin-right: auto;
      font-size: 30px;
      margin-bottom: 5px; } }
  &__link-wrap {
    text-align: center;
    padding-top: 50px;
    @include lg {
      padding-top: 30px; }
    @include md {
      padding-top: 20px; }
    @include sm {
      padding-top: 15px; } } }

.s-services {
  .word {
    right: -7%; } }

.s-we-know {
  overflow: hidden;
  .word {
    right: -13%; }
  &__title-block {
    @include css-lock(30, 240, 320, 1499, margin-bottom); }
  &__title {
    max-width: 650px;
    @include css-lock(15, 80, 320, 1499, margin-bottom);
    .title {
      opacity: 0; } }
  &__content {
    max-width: 930px;
    @include css-lock(15, 95, 320, 1499, margin-bottom); }
  &__text {
    opacity: 0; } }

.s-testimonials {
  position: relative;
  @include before {
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0;
    top: 0;
    z-index: 0;
    background-image: linear-gradient( 0deg, rgb(46,5,56) 0%, rgb(2,2,2) 100%); }
  &__title {
    position: relative;
    z-index: 1;
    margin-bottom: 95px;
    max-width: 1000px;
    .title {
      opacity: 0; }
    @include xl {
      margin-bottom: 75px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__slider {
    position: relative;
    z-index: 1; } }

.s-order {
  &__inner {
    @include flex(flex-start, normal, row, wrap);
    overflow: hidden; }
  &__left {
    width: percentage(8/12);
    padding-right: 15px;
    @include md {
      width: 60%; }
    @include sm {
      width: 100%;
      margin-bottom: 15px;
      padding-right: 0; } }
  &__right {
    width: percentage(4/12);
    opacity: 0;
    @include md {
      width: 40%; }
    @include sm {
      width: 100%; } }
  &__title {
    margin-bottom: 55px;
    .title {
      opacity: 0; }
    @include xl {
      margin-bottom: 45px; }
    @include lg {
      margin-bottom: 35px; }
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__contacts {
    margin-bottom: 50px;
    opacity: 0;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__btns {
    @include flex(flex-start, normal, row, wrap);
    opacity: 0;
    @include xl {
      display: block; } }
  &__btn {
    width: percentage(3/8);
    @include notlast {
      margin-right: 30px; }
    @include xl {
      width: 50%;
      @include notlast {
        margin-right: 0;
        margin-bottom: 15px; } }
    @include lg {
      width: 60%; }
    @include md {
      width: 70%; }
    @include sm {
      width: auto; } } }


.order-contacts {
  &__mail {
    text-transform: uppercase;
    font-size: 18px;
    margin-bottom: 10px;
    a {
      @include tr(color .3s);
      @include hover {
        color: $gray; } }
    @include sm {
      font-size: $fz; } }
  &__phone {
    @extend .title;
    @include reg;
    a {
      @include tr(color .3s);
      @include hover {
        color: $gray; } } }
  &__text {
    text-transform: uppercase;
    margin-top: 10px; } }
