.services {}


.service-item {
  $this: &;
  display: block;
  @include css-lock(30, 85, 320, 1499, padding-top);
  @include css-lock(30, 85, 320, 1499, padding-bottom);
  color: $white;
  background-color: $purple-dark;
  position: relative;
  overflow: hidden;
  transform: translate(-100%, 0);
  @include tr(color .5s, transform .4s ease-out);
  @include notlast {
    border-bottom: 1px solid  $purple; }
  @include before {
    @include coverdiv;
    background-color: $white;
    z-index: 0;
    transform: translate(-100%, 0);
    @include tr(transform .5s); }
  @include hover {
    color: $black;
    border-bottom-color: transparent;
    @include before {
      transform: translate(0, 0); }
    #{$this}__link {
      transform: translate(0, 0); } }
  &__wrap {
    overflow: hidden;
    &.is-show {
      #{$this} {
        transform: translate(0, 0); } } }
  &__inner {
    position: relative;
    z-index: 1;
    @include flex(space-between, center); }

  &__title {
    margin-bottom: 30px;
    display: block;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; } }
  &__block {
    max-width: 590px;
    display: block;
    padding-right: 10px; }
  &__link {
    display: block;
    @include tr(transform .3s);
    .icon {
      font-size: $fz-arrow;
      @include tr(transform .3s);
      @include sm {
        font-size: $fz-arrow-sm; } }
    .no-touch & {
      transform: translate(-20px, 0); } }
  &__text {
    display: block; } }
