.footer {
  background-color: $black;
  color: $white;
  overflow: hidden;
  @include xs {
    text-align: center; }
  &__logo {
    background-color: $white;
    padding: 10px;
    font-size: 0;
    margin-right: 20px;
    .icon {
      font-size: 30px;
      fill: $black; }
    @include sm {
      margin-right: 15px; }
    @include xs {
      margin-right: 0;
      width: 50px;
      margin-bottom: 10px;
      margin-left: auto;
      margin-right: auto; } }
  &__inner {
    @include flex(flex-start, center);
    opacity: 0;
    @include xs {
      display: block; } }
  &__meta {
    flex-grow: 1;
    text-transform: uppercase;
    font-size: 10px;
    padding-right: 15px;
    @include xs {
      padding-right: 10px;
      margin-bottom: 15px; }
    p {
      @include notlast {
        margin-bottom: 5px; } }
    a {
      text-decoration: underline; } }
  &__scroll-top {
    height: 100%;
    @include flex(flex-start, flex-end);
    @include xs {
      .scroll-to {
        margin-left: auto;
        margin-right: auto; } } } }
